import React, { useEffect } from 'react';
import {
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Typography,
    Link,
    Divider,
    Grid,
    TableContainer,
    Table,
    TableHead,
    TableCell,
    TableRow,
    TableBody,
    CircularProgress
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import useStyles from '../../../ViewDialogStyles';
import { getModuleSubscriptionById } from '../../../../services/AddModuleSubscription';
import { useState } from 'react';
import { withStyles } from '@material-ui/styles';
const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function ViewBatchIds(props) {
    const { open, close, data } = props;
    const [batches, setBatches] = useState([]);
    const [loading, setLoading] = useState(true);
    const classes = useStyles();
    const handlePopupClose = () => {
        close(false);
    };
    const fetchBatchData = async (subscriptionId) => {
        try {
            const res = await getModuleSubscriptionById(subscriptionId);
            setBatches(res);
            setLoading(false);
        } catch (error) {
            console.log(error);
            setLoading(false);
        }
    };
    useEffect(() => {
        if (data) {
            fetchBatchData(data);
        }
    }, [data]);
    const formatStartDateTime = (dateTime) => {
        const formattedDateTime = new Date(dateTime).toLocaleString();
        return formattedDateTime;
    };
    return (
        <div>
            <Dialog open={open} maxWidth={'sm'}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: 'px' }}>Batch Ids</Typography>
                        <Link to="#" onClick={handlePopupClose} style={{ fontSize: '16px' }}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <DialogContent style={{ maxHeight: '350px' }}>
                    <Grid container>
                        <TableContainer>
                            <Table aria-label="simple table">
                                <TableHead className={classes.tableHeaderCell}>
                                    <StyledTableRow>
                                        <TableCell align="center"> Batch Id</TableCell>
                                        <TableCell align="center"> Batch Name</TableCell>
                                        <TableCell align="center"> Max Age</TableCell>
                                        <TableCell align="center"> Max Student</TableCell>
                                        <TableCell align="center"> Min Age</TableCell>
                                        <TableCell align="center"> Student Count</TableCell>
                                        <TableCell align="center"> Teacher Count </TableCell>
                                        <TableCell align="center"> StartDate Time </TableCell>
                                    </StyledTableRow>
                                </TableHead>
                                {loading ? (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell colSpan={8} style={{ textAlign: 'center' }}>
                                                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '200px' }}>
                                                    <CircularProgress />
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                ) : batches?.batch?.length === 0 ? (
                                    <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                                ) : (
                                    <TableBody>
                                        {batches?.batch?.map((item) => (
                                            <TableRow key={item.id}>
                                                <TableCell align="center">{item?.id}</TableCell>
                                                <TableCell align="center">{item?.name}</TableCell>
                                                <TableCell align="center">{item?.maxAge}</TableCell>
                                                <TableCell align="center">{item?.maxStudent}</TableCell>
                                                <TableCell align="center">{item?.minAge}</TableCell>
                                                <TableCell align="center">{item?.studentCount}</TableCell>
                                                <TableCell align="center">{item?.teacherCount}</TableCell>
                                                <TableCell align="center">{formatStartDateTime(item?.startDateTime)}</TableCell>
                                            </TableRow>
                                        ))}
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Grid>
                </DialogContent>

                <Divider style={{ marginTop: 15 }} />
                <DialogActions className={classes.dialogAction}>
                    <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={handlePopupClose}>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
