import React, { useEffect, useState } from 'react';
import MainCard from '../../ui-component/cards/MainCard';
import SubjectIcon from '@material-ui/icons/Subject';
import useStyles from './styles';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import VisibilityIcon from '@material-ui/icons/Visibility';
import { withStyles } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Typography,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import EditIcon from '@material-ui/icons/Edit';

import useUserRole from '../../hooks/useUserRole';
import { getSubscriptions } from '../../services/subscriptions';
import AddOnlineSubscription from './addSubscription';
import ViewBatchIdsInSubScription from './viewSubscription/viewBatchIdInSubscription';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

function Subscriptions() {
    useUserRole({ roleList: ['ADMIN', 'TEACHER','SUPER_ADMIN'] });
    const classes = useStyles();
    const history = useHistory();
    const { loggedUserProfile } = useSelector((state) => state?.userProfile);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [isSearch, setSearch] = useState('');
    const [openAddSubscription, setOpenAddSubscription] = useState(false);
    const [isSubscriptionUpdateData, setSubscriptionUpdateData] = useState(null);

    const [rowIds, setRowIds] = useState([]);

    const [openBachIdPopup, setOpenBachIdPopup] = useState(false);
    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleSearch = (event) => {
        setSearch(event.target.value);
    };

    const [subscriptionData, setSubscriptionData] = useState([]);

    const GetSubscriptions = async () => {
        await getSubscriptions(page + 1, rowsPerPage, isSearch ? isSearch : '')
            .then((res) => {
                setSubscriptionData(res?.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const handleViewSubscription = (id) => {
        history.push(`/admin/module-subscriptions/${id}`);
    };

    useEffect(() => {
        GetSubscriptions(page + 1, rowsPerPage, isSearch ? isSearch : '');
    }, [page, rowsPerPage, isSearch]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; Subscription Modules </span>
                </div>

                <Paper elevation={5} className={classes.root}>
                    <Grid container spacing={3} style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Grid item xs={12} sm={6} md={6}>
                            <div>
                                <OutlinedInput
                                    className={classes.searchControl}
                                    id="input-search-header"
                                    onChange={(event) => handleSearch(event)}
                                    placeholder="Search"
                                    startAdornment={
                                        <InputAdornment position="start">
                                            <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                        </InputAdornment>
                                    }
                                />
                            </div>
                        </Grid>
                        {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                            <Grid item xs={12} sm={6} md={6}>
                                <div className={classes.btn}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        className={classes.btnAddPlanOptions}
                                        onClick={() => {
                                            setOpenAddSubscription(true);
                                            setSubscriptionUpdateData(null);
                                        }}
                                        style={{ cursor: 'pointer' }}
                                    >
                                        Add
                                    </Button>
                                </div>
                            </Grid>
                        )}
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Full Name(Profile Id) </TableCell>
                                    <TableCell> Module Title </TableCell>
                                    <TableCell> Status </TableCell>
                                    <TableCell> Payment-Status </TableCell>
                                    <TableCell> Batch Ids </TableCell>
                                    <TableCell align="center"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!subscriptionData?.content ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {subscriptionData?.content?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell>
                                                {row?.userLimitedInfo?.fullName}({row?.userLimitedInfo?.profileId})
                                            </TableCell>
                                            <TableCell> {row?.moduleLimitedInfo?.title} </TableCell>
                                            <TableCell> {row?.status} </TableCell>

                                            <TableCell> {row?.paymentStatus} </TableCell>
                                            <TableCell style={{ fontSize: '15px' }}>
                                                {row?.id && (
                                                    <Button
                                                        variant="contained"
                                                        color="secondary"
                                                        size="small"
                                                        onClick={() => {
                                                            setRowIds(row.id);
                                                            setOpenBachIdPopup(true);
                                                        }}
                                                    >
                                                        BatchIds
                                                    </Button>
                                                )}
                                            </TableCell>

                                            {(loggedUserProfile?.role === 'ADMIN' || loggedUserProfile?.role === 'SUPER_ADMIN') && (
                                                <TableCell align="right">
                                                    <Tooltip title="View Subscription">
                                                        <IconButton onClick={() => handleViewSubscription(row?.id)}>
                                                            <VisibilityIcon
                                                                color="primary"
                                                                fontSize="small"
                                                                style={{ cursor: 'pointer' }}
                                                            />
                                                        </IconButton>
                                                    </Tooltip>
                                                </TableCell>
                                            )}
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 15, 25]}
                        component="div"
                        count={subscriptionData?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>

                {openAddSubscription && (
                    <AddOnlineSubscription
                        isOpen={openAddSubscription}
                        setOpen={setOpenAddSubscription}
                        isEditData={isSubscriptionUpdateData}
                        getAllSubscription={GetSubscriptions}
                    />
                )}
                {openBachIdPopup && <ViewBatchIdsInSubScription data={rowIds} open={openBachIdPopup} close={setOpenBachIdPopup} />}
            </MainCard>
        </>
    );
}

export default Subscriptions;
