import React, { useState, useEffect } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import CloseIcon from '@material-ui/icons/Close';
import { useDispatch, useSelector } from 'react-redux';
import { setMessage } from '../../../store/actions/MessageActions';
import useStyles from '../../DialogStyles';
import { userProfileSearch } from '../../../store/actions/AddUserActions';

import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Typography,
    Grid,
    Link,
    Divider,
    TextField,
    Autocomplete,
    Paper
} from '@material-ui/core';
import * as yup from 'yup';
import { RHFSelect, FormProvider, RHFTextField } from '../../../components/hook-form';
import { getAllActiveCourses, getAllActiveModules } from '../../../services/courseCard';
import { addModuleSubscription } from '../../../store/actions/FAQActions';
import { calculateModule, checkDiscountCode, checkVoucherCode } from '../../../services/modulePlanSubscriptions';
import { getSubscriptions } from '../../../services/subscriptions';
import { FormControl } from '@mui/material';

const validationSchema = yup.object({
    courseId: yup.string('Select course').required('Course Is Required'),
    moduleId: yup.string('Select module').required('Module Is Required'),
    userId: yup.string('Select user ID').required('User ID Is Required'),
    profileId: yup.string('Select profile ID').required('Profile ID Is Required'),
    //discountCode: yup.string('Select discount Code'),
    //giftVoucherCode: yup.string('Select voucher Code'),
    paymentMode: yup.string('Select payment mode').required('Payment mode Is Required'),
    pricePaid: yup.string('Select price').required('Price Is Required'),
    transactionId: yup.string('Select transaction')
});

const STATUS_ITEMS = [
    { id: 0, status: 'ONLINE' },
    { id: 1, status: 'CASH' },
    { id: 2, status: 'NOT_APPLICABLE' },
    { id: 3, status: 'NEFT' },
    { id: 4, status: 'GPAY_WALLET' },
    { id: 5, status: 'RAZORPAY' },
    { id: 6, status: 'FREE' }
];

export default function AddOnlineSubscription (props) {
    const { isOpen, setOpen, batchId, isEditData, getAllSubscription } = props;
    const [userId, setUserId] = useState(null);
    const [profileId, setProfileId] = useState(null);
    const classes = useStyles();
    const dispatch = useDispatch();
    const [selectedOptions, setSelectedOptions] = useState(null);
    const { getSearchedProfileData } = useSelector((state) => state?.addUser);
    const [total, setTotal] = useState('');

    const handlePopupClose = () => {
        setOpen(false);
    };

    const handleChange = (event, value) => {
        setSelectedOptions(value);
        setUserId(value?.userId);
        setProfileId(value?.profileId);
    };

    const handleInputChange = (event, value) => {
        dispatch(userProfileSearch(value, 'STUDENT'));
    };

    const [moduleId, setmoduleId] = useState('');
    const [paymentMode, setPaymentMode] = useState('');
    const [pricePaid, setPricePaid] = useState('');
    const [transactionId, setTransactionId] = useState('');
    const [discountCode, setDiscountCode] = useState('');
    const [giftVoucherCode, setGiftVoucherCode] = useState('');
    const [price, setPrice] = useState('');

    const handleSubmitData = () => {
        if (discountCode !== '' && giftVoucherCode !== '') {
            const payload1 = {
                moduleId: moduleId,
                userId: userId,
                profileId: profileId,
                discountCode: discountCode,
                giftVoucherCode: giftVoucherCode,
                paymentMode: paymentMode,
                pricePaid: price,
                transactionId: transactionId
            };
            dispatch(
                addModuleSubscription(payload1, () => {
                    getAllSubscription();
                })
            );
        } else {
            const payload2 = {
                moduleId: moduleId,
                userId: userId,
                profileId: profileId,
                paymentMode: paymentMode,
                pricePaid: price,
                transactionId: transactionId
            };
            dispatch(
                addModuleSubscription(payload2, () => {
                    getAllSubscription();
                })
            );
        }

        handlePopupClose();
    };

    const [storedprice, setStoredprice] = useState('');

    const CouponcodeCheckAndApply = async () => {
        const DiscountCodepayload = {
            moduleId: moduleId,
            userId: userId,
            profileId: profileId,
            discountCode: discountCode,
            giftVoucherCode: giftVoucherCode
        };
        await checkDiscountCode(DiscountCodepayload, dispatch, setMessage).then((res) => {
            calculateModule(DiscountCodepayload)
                .then((res) => {
                    setTotal(res);
                    setStoredprice(res?.priceToBePaid);

                    if (!storedprice) {
                        setPrice(res?.priceToBePaid);
                    } else {
                        let a = price - res?.priceToBePaid;
                        setPrice(a);
                    }
                })
                .catch((error) => {
                    console.log(error);
                });
        });
    };

    const handleDiscountCode = (e) => {
        CouponcodeCheckAndApply(dispatch, setMessage);
    };

    const VouchercodeCheckAndApply = async () => {
        const Voucherpayload = {
            moduleId: moduleId,
            userId: userId,
            profileId: profileId,
            giftVoucherCode: giftVoucherCode,
            discountCode: discountCode
        };
        await checkVoucherCode(Voucherpayload, dispatch, setMessage).then((res) => {
            calculateModule(Voucherpayload)
                .then((res) => {
                    setTotal(res);
                    setStoredprice(res?.priceToBePaid);
                    if (!storedprice) {
                        setPrice(res?.priceToBePaid);
                    } else {
                        let b = price - res?.priceToBePaid;
                        setPrice(b);
                    }
                })
                .catch((error) => {
                    console.log(error);
                });
        });
    };

    const handleVoucherCode = (e) => {
        VouchercodeCheckAndApply(dispatch, setMessage);
    };

    const handleCourseChange = (event) => {
        GetAllModules(event?.target?.value);
    };

    const handleModuleChange = (event) => {
        const val = event.target.value;

        setmoduleId(val);
        const payload = {
            moduleId: val,
            userId: userId,
            profileId: profileId
        };

        if (val) {
            calculateModule(payload)
                .then((res) => {
                    setTotal(res);
                    setPrice(res?.priceToBePaid);
                })
                .catch((error) => {
                    console.log(error);
                });
        }
    };

    const [courseList, setCourseList] = useState([]);
    const GetAllCourses = async () => {
        await getAllActiveCourses()
            .then((res) => {
                setCourseList(res?.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const [moduleList, setModuleList] = useState([]);

    const GetAllModules = async (id) => {
        await getAllActiveModules(id)
            .then((res) => {
                setModuleList(res?.data);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    useEffect(() => {
        dispatch(userProfileSearch('', 'STUDENT'));
        GetAllCourses();
    }, []);

    return (
        <div>
            <Dialog open={isOpen}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}> Add Subscription </Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormControl variant="outlined">
                    {/* <PerfectScrollbar className={classes.ScrollHeight}> */}
                    <DialogContent>
                        <Grid container>
                            <Grid item xs={12}>
                                <label>
                                    Users <span className={classes.mandatory}> * </span>
                                </label>
                                <Autocomplete
                                    autoHighlight
                                    id="students"
                                    options={getSearchedProfileData}
                                    getOptionLabel={(option) => option.emailFirstNameLastName}
                                    onChange={handleChange}
                                    onInputChange={handleInputChange}
                                    filterSelectedOptions
                                    renderInput={(params) => <TextField {...params} variant="outlined" />}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <label>
                                    Course <span className={classes.mandatory}> * </span>
                                </label>
                                <select name="courseId" sx={{ width: 'fullwidth' }} label="" onChange={handleCourseChange}>
                                    <option value=""> Choose a course </option>
                                    {courseList?.map((item, index) => (
                                        <option
                                            key={index}
                                            value={item?.id}
                                            defaultValue={
                                                item?.id === courseList?.filter((lang) => lang.id === isEditData?.courseId)[0]?.id
                                            }
                                        >
                                            {item?.title}
                                        </option>
                                    ))}
                                </select>
                            </Grid>
                            <br />
                            <Grid item xs={12}>
                                <label>
                                    Module <span className={classes.mandatory}>*</span>
                                </label>
                                <select
                                    name="moduleId"
                                    // onChange={(e) => setmoduleId(e.target.value)}
                                    onChange={handleModuleChange}
                                    label=""
                                >
                                    <option value=""> Choose a module </option>
                                    {moduleList?.map((item, index) => (
                                        <option
                                            key={item?.id}
                                            value={item?.id}
                                            defaultValue={
                                                item?.id === moduleList?.filter((lang) => lang.id === isEditData?.moduleId)[0]?.id
                                            }
                                        >
                                            {item?.title}
                                        </option>
                                    ))}
                                </select>
                            </Grid>
                            <br />
                            <Grid style={{ display: 'flex', justifyContent: 'space-between' }}>
                                {/* <Paper style={{ height: '150px', width: '300px', display: 'flex', justifyContent: 'center' }}> */}
                                {/* <FormProvider
                                        methods={methods}
                                        style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
                                    > */}
                                <Grid style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                                    <Typography style={{ fontWeight: 'lighter', fontSize: '12px', marginTop: '16px', color: '#3fb2cf' }}>
                                        Voucher Code
                                    </Typography>
                                    &nbsp;
                                    <TextField
                                        fullWidth
                                        name="giftVoucherCode"
                                        onChange={(e) => setGiftVoucherCode(e.target.value)}
                                        value={giftVoucherCode}
                                        className={classes.rhf}
                                    />
                                    &nbsp;
                                    <Button
                                        variant="contained"
                                        size="small"
                                        onClick={handleVoucherCode}
                                        marginTop="10px"
                                        style={{ marginTop: '10px' }}
                                    >
                                        Apply
                                    </Button>
                                </Grid>
                                &nbsp;
                                {/* </FormProvider> */}
                                {/* </Paper> */}
                                <Grid style={{ display: 'flex', justifyContent: 'center' }}>
                                    {/* <Paper style={{ height: '150px', width: '300px', display: 'flex', justifyContent: 'center' }}> */}
                                    {/* <FormProvider methods={methods}> */}
                                    <Typography style={{ fontWeight: 'lighter', fontSize: '12px', marginTop: '16px', color: '#3fb2cf' }}>
                                        Discount Coupon
                                    </Typography>
                                    &nbsp;
                                    <TextField
                                        fullWidth
                                        name="discountCode"
                                        className={classes.rhf}
                                        value={discountCode}
                                        onChange={(e) => setDiscountCode(e.target.value)}
                                        // onChange={(e) => {
                                        //     setCouponCode(e.target.value);
                                        // }}
                                    />
                                    &nbsp;
                                    <Button variant="contained" onClick={handleDiscountCode} size="small" style={{ marginTop: '10px' }}>
                                        Apply
                                    </Button>
                                    {/* </FormProvider> */}
                                    {/* </Paper> */}
                                </Grid>
                            </Grid>
                            <Grid display="flex">
                                <Grid item xs={12} sm={6}>
                                    <label className={classes.label}>
                                        Payment Mode <span className={classes.mandatory}>*</span>
                                    </label>
                                    <select name="paymentMode" onChange={(e) => setPaymentMode(e.target.value)} label="" required>
                                        <option value=""> Select Status </option>

                                        {STATUS_ITEMS?.map((item, index) => (
                                            <option key={index} value={item?.status}>
                                                {item?.status}
                                            </option>
                                        ))}
                                    </select>
                                </Grid>
                                &nbsp;
                                <Grid item xs={12} sm={6}>
                                    <label className={classes.label}>
                                        Price <span className={classes.mandatory}>*</span>
                                    </label>
                                    <TextField
                                        disabled
                                        value={`${total?.priceToBePaid ? total?.priceToBePaid : ''}`}
                                        fullWidth
                                        name="pricePaid"
                                        required
                                    />
                                </Grid>
                            </Grid>
                            <br /> <br />
                            <Grid item xs={12}>
                                <label className={classes.label}> Transaction ID </label>
                                <TextField fullWidth name="transactionId" onChange={(e) => setTransactionId(e.target.value)} />
                            </Grid>
                        </Grid>
                    </DialogContent>
                    {/* </PerfectScrollbar> */}
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="secondary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" onClick={handleSubmitData} className={classes.buttonSubmit}>
                            Add
                        </Button>
                        {/* <Button variant="contained" type="submit" className={classes.buttonSubmit}>
                            Add
                        </Button> */}
                    </DialogActions>
                </FormControl>
            </Dialog>
        </div>
    );
}
